const initState = {
    loading: false
}

type ActionsType = LoadingType

export type LoadingType = {
    type: 'LOADING'
    status: boolean
}

export const loadingReducer = (state = initState, action: ActionsType): typeof initState=> { // fix any
    switch (action.type) {
        case 'LOADING': {
            return {...state, loading: action.status}
        }
        default: return state
    }
}

export const loadingAC = (status: boolean): LoadingType => ({type: 'LOADING', status}) // fix any