import React, {MouseEventHandler} from 'react'
import Affair from './Affair'
import {AffairType, FilterType} from './HW2'
import s from './Affairs.module.css'

type AffairsPropsType = { // need to fix any
    data: AffairType[]
    setFilter: (filter: FilterType) => void
    deleteAffairCallback: (id: number) => void // почему войд если возврвщает массив
    filter: FilterType
}

function Affairs(props: AffairsPropsType) {
    const mappedAffairs = props.data.map((a: AffairType) => (
        <Affair // should work
            key={a._id} // кеи ОБЯЗАТЕЛЬНЫ в 99% - так что лучше их писать всегда при создании компонент в мапе
            affair={a}
            deleteAffairCallback={props.deleteAffairCallback}
        />
    ))

    const set = (e: React.MouseEvent<HTMLButtonElement>) => {
        props.setFilter(e.currentTarget.value as FilterType)
    }

    const btnAll = s.btn + ' ' + (props.filter === 'all' ? s.active : '')
    const btnHigh = s.btn + ' ' + (props.filter === 'high' ? s.active : '')
    const btnMiddle = s.btn + ' ' + (props.filter === 'middle' ? s.active : '')
    const btnLow = s.btn + ' ' + (props.filter === 'low' ? s.active : '')

    const setClass = (filter: FilterType) => {
        return s.btn + ' ' + (props.filter === filter ? s.active : '')
    }

    return (
        <div>

            {mappedAffairs}

            <button onClick={set} value={'all'} className={btnAll}>All</button>
            <button onClick={set} value={'high'} className={btnHigh}>High</button>
            <button onClick={set} value={'middle'} className={btnMiddle}>Middle</button>
            <button onClick={set} value={'low'} className={btnLow}>Low</button>

            <button onClick={set} value={'all'} className={setClass('all')}>All</button>
            <button onClick={set} value={'high'} className={setClass('high')}>High</button>
            <button onClick={set} value={'middle'} className={setClass('middle')}>Middle</button>
            <button onClick={set} value={'low'} className={setClass('low')}>Low</button>
        </div>
    )
}

export default Affairs
